<el-form-item label=\"类型\" :label-width=\"formLabelWidth\" prop=\"hash\">\r\n          <el-select v-model=\"hash\" placeholder=\"请选择类型\">\r\n            <el-option\r\n              v-for=\"item in hashOptions\"\r\n              :key=\"item.value\"\r\n              :label=\"item.label\"\r\n              :value=\"item.value\"\r\n            ></el-option>\r\n          </el-select>\r\n        </el-form-item>\r\n        <div v-show=\"hash ===0\">\r\n          <el-form-item label=\"开始\" :label-width=\"formLabelWidth\" prop=\"start\">\r\n            <el-input v-model=\"ruleForm.start\" auto-complete=\"off\"></el-input>\r\n          </el-form-item>\r\n          <el-form-item label=\"结束\" :label-width=\"formLabelWidth\" prop=\"end\">\r\n            <el-input v-model=\"ruleForm.end\" auto-complete=\"off\"></el-input>\r\n          </el-form-item>\r\n          <el-form-item label=\"步长\" :label-width=\"formLabelWidth\" prop=\"step\">\r\n            <el-input v-model=\"ruleForm.step\" auto-complete=\"off\"></el-input>\r\n          </el-form-item>\r\n        </div>\r\n      </el-form>\r\n      <div slot=\"footer\" class=\"dialog-footer\">\r\n        <el-button @click=\"resetForm('ruleForm')\">重置</el-button>\r\n        <span class=\"wd\">&nbsp;</span>\r\n        <el-button type=\"primary\" @click=\"submit('ruleForm')\">确认</el-button>\r\n      </div>\r\n    </div>\r\n  </el-dialog>\r\n</template>\r\n\r\n<style scoped>\r\n.el-dialog__wrapper .el-dialog--customer .el-dialog__header {\r\n  text-align: center;\r\n}\r\n\r\n.dialog-form {\r\n  width: 380px;\r\n}\r\n\r\n.el-select {\r\n  width: 200px;\r\n}\r\n\r\n.el-input {\r\n  width: 200px;\r\n}\r\n\r\n.el-select .el-input {\r\n  width: 260px;\r\n}\r\n\r\n.el-select-dropdown__item {\r\n  max-width: 500px;\r\n}\r\n\r\n.dialog-footer {\r\n  text-align: center;\r\n}\r\n\r\n.el-upload ul {\r\n  display: none;\r\n}\r\n\r\n.download-btn {\r\n  text-align: center;\r\n  display: inline-block;\r\n  font-size: 12px;\r\n  border-radius: 5px;\r\n  