ssage: \"请输入释义\", trigger: \"blur\" },\r\n          {\r\n            max: 24,\r\n            message: \"文案限制24个字符，包括标点符号\",\r\n            trigger: \"change\",\r\n          },\r\n        ],\r\n        //          hash: [\r\n        //            { required: true, message: '请选择属性类型', trigger: 'change' }\r\n        //          ],\r\n        step: [{ require: true, message: \"请输入范围步长\", trigger: \"blur\" }],\r\n        start: [{ require: true, message: \"请输入开始范围\", trigger: \"blur\" }],\r\n        end: [{ require: true, message: \"请输入结束范围\", trigger: \"blur\" }],\r\n      },\r\n      dialogVisible: false,\r\n    };\r\n  },\r\n  computed: {},\r\n  methods: {\r\n    submit(formName) {\r\n      if (!this.ruleForm.xml) {\r\n        this.$message(\"请输入xml属性字段\");\r\n        return false;\r\n      }\r\n      if (!this.ruleForm.name) {\r\n        this.$message(\"请输入释义\");\r\n        return false;\r\n      } else {\r\n        if (this.ruleForm.name.length > 24) {\r\n          this.$message(\"文案限制24个字符，包括标点符号\");\r\n          return false;\r\n        }\r\n      }\r\n\r\n      if (this.hash === 0) {\r\n        if (!this.ruleForm.start) {\r\n          this.$message(\"开始范围不能为空！\");\r\n          return false;\r\n        }\r\n        if (!this.ruleForm.end) {\r\n          this.$message(\"结束范围不能为空！\");\r\n          return false;\r\n        }\r\n        if (!this.ruleForm.step) {\r\n          this.$message(\"范围步长不能为空！\");\r\n          return false;\r\n        }\r\n      }\r\n\r\n      this.ruleForm.hash = this.hash;\r\n\r\n      this.$emit(\"submitForm\", this.dialogInfo.dialogType, this.ruleForm);\r\n    },\r\n\r\n    resetForm(formName) {\r\n      if (this.$refs[formName]) {\r\n        this.$refs[formName].resetFields();\r\n        this.ruleForm = {\r\n          uuid: \"\",\r\n          xml: \"\",\r\n          name: \"\",\r\n          hash: 1,\r\n          step: \"\",\r\n          start: \"\",\r\n          end: \"\",\r\n        };\r\n        this.hash = 0;\r\n      }\r\n    },\r\n    closeDialog() {\r\n      //console.log('closeDialog');\r\n      this.$emit(\"closeDialog\");\r\n    },\r\n  },\r\n  created() {},\r\n  mounted() {\r\n    this.ruleForm = this.dialogInfo.form;\r\n  },\r\n  watch: {\r\n    dialogInfo: {\r\n      handler: function(val, oldVal) {\r\n        if (val.dialogType == \"add\") {\r\n          this.resetForm(\"ruleForm\");\r\n        } else {\r\n          this.ruleForm = this.dialogInfo.form;\r\n          this.hash = this.ruleForm.hash;\r\n          //console.log(this.ruleForm);\r\n        }\r\n      },\r\n      deep: true,\r\n    },\r\n    //      id:function (val,oldVal) {\r\n    //        this.ruleForm = this.dialogInfo.form;\r\n    //      }\r\n    dialogFormVisible: function(val) {\r\n      this.dialogVisible = !!val;\r\n    },\r\n  },\r\n};\r\n</script>\r\n"]}]);
