exports.push([module.id, "\n.el-table .cell,\r\n.el-table th > div {\r\n  padding-left: 5px;\r\n  padding-right: 5px;\r\n  box-sizing: border-box;\r\n  text-overflow: ellipsis;\n}\r\n", "",{"version":3,"sources":["src/views/contents/risk/monitoringAttributesManagement.vue"],"names":[],"mappings":";AAiFA;;EAEA,iBAAA;EACA,kBAAA;EACA,sBAAA;EACA,uBAAA;AACA","file":"monitoringAttributesManagement.vue?vue&type=style&index=1&lang=css&","sourcesContent":["<!--\r\n * @Description: \r\n * @Author: 焦丙乾(jiaobingqian)\r\n * @Email: jiaobq123@163.com\r\n * @Date: 2019-07-15 18:33:54\r\n * @LastEditTime: 2019-07-17 19:42:45\r\n * @LastEditors: 王娇(wangjiao)\r\n -->\r\n<template>\r\n  <div>\r\n    <tab-page ref=\"tabPage\">\r\n      <template slot=\"firstTabPage\">\r\n        <data-table\r\n          :data-table=\"dataTable\"\r\n          ref=\"dataTable\"\r\n          style=\"width: 100%\"\r\n          border\r\n          :default-sort=\"{prop: 'date', order: 'descending'}\"\r\n        >\r\n          <!--<data-table :data-table=\"dataTable\" ref=\"dataTable\">-->\r\n          <div slot=\"conditions\">\r\n            <div class=\"opera-btn\">\r\n              <el-button type=\"primary\" size=\"small\" @click=\"add\">&nbsp;新增属性</el-button>\r\n            </div>\r\n          </div>\r\n          <template slot=\"columns\">\r\n            <el-table-column prop=\"uuid\" label=\"uuid\" align=\"center\" width=\"70\" sortable></el-table-column>\r\n            <el-table-column prop=\"xml\" label=\"变量XML字段\" align=\"center\" sortable></el-table-column>\r\n            <el-table-column prop=\"name\" label=\"释义\" align=\"center\" sortable></el-table-column>\r\n            <el-table-column\r\n              prop=\"hash\"\r\n              label=\"类型\"\r\n              align=\"center\"\r\n              width=\"80\"\r\n              :formatter=\"formatterType\"\r\n              sortable\r\n            ></el-table-column>\r\n            <el-table-column prop=\"step\" label=\"步长\" align=\"center\" width=\"80\" sortable></el-table-column>\r\n            <el-table-column prop=\"start\" label=\"开始范围\" align=\"center\" width=\"100\" sortable></el-table-column>\r\n            <el-table-column prop=\"end\" label=\"结束范围\" align=\"center\" width=\"100\" sortable></el-table-column>\r\n\r\n            <el-table-column prop=\"created_at\" label=\"创建日期\" align=\"center\" width=\"100\" sortable></el-table-column>\r\n            <el-table-column prop=\"updated_at\" label=\"更新日期\" align=\"center\" width=\"100\" sortable></el-table-column>\r\n            <el-table-column label=\"操作\" width=\"120\" align=\"center\">\r\n              <template slot-scope=\"scope\">\r\n                <el-button type=\"primary\" size=\"small\" @click=\"edit(scope.row)\">编辑</el-button>\r\n                <el-button type=\"danger\" size=\"small\" @click=\"remove(scope.row)\">删除</el-button>\r\n              </template>\r\n            </el-table-column>\r\n          </template>\r\n        </data-table>\r\n\r\n        <attribute-form\r\n          :dialogInfo=\"dialogInfos\"\r\n          :dialogFormVisible=\"showDialog\"\r\n          @submitForm=\"submitFormHandle\"\r\n          v-on:closeDialog=\"closeFormDialog\"\r\n        ></attribute-form>\r\n      </template>\r\n    </tab-page>\r\n  </div>\r\n</template>\r\n\r\n<style scoped>\r\n.el-select .el-input {\r\n  width: 100px;\r\n}\r\n\r\n.pagination_position {\r\n  padding-top: 20px;\r\n}\r\n\r\n.echarts {\r\n  height: 300px;\r\n}\r\n\r\n.opera-btn {\r\n  margin-bottom: 10px;\r\n}\r\n</style>\r\n<style>\r\n.el-table .cell,\r\n.el-table th > div {\r\n  padding-left: 5px;\r\n  padding-right: 5px;\r\n  box-sizing: border-box;\r\n  text-overflow: ellipsis;\r\n}\r\n</style>\r\n<script>\r\nimport DataTable from '../../../components/DataTable';\r\nimport TabPage from '../../../components/TabPage';\r\n\r\nimport Common from '../../../utils/common';\r\nimport formatDate from '../../../utils/formatDate.js';\r\n\r\nimport attributeForm from './attributeForm.vue';\r\n\r\nexport default {\r\n  components: {\r\n    DataTable,\r\n    TabPage,\r\n    attributeForm\r\n  },\r\n  data() {\r\n    let date = new Date();\r\n    let prevMonth = new Date(date.setDate(date.getDate() - 30));\r\n    let now = new Date();\r\n\r\n    function randomData() {\r\n      return Math.round(Math.random() * 1000);\r\n    }\r\n\r\n    return {\r\n      detailData: {},\r\n      deviceOptions: [\r\n        {\r\n          value: 'ALL',\r\n          name: 'ALL'\r\n        },\r\n        {\r\n          value: 'iPhone',\r\n          name: 'iOS'\r\n        },\r\n        {\r\n          value: 'Android',\r\n          name: 'Android'\r\n        }\r\n      ],\r\n      searchParam: {\r\n        device: 'ALL',\r\n        dateRange: [prevMonth, now]\r\n      },\r\n      dataTable: {\r\n        pageSize: 10,\r\n        pageSizes: [10, 20, 50, 100, 1000],\r\n        type: 'get',\r\n        url: '/web/v1/attribute',\r\n        queryParams: {},\r\n        emptyText: '没有记录'\r\n      },\r\n      loading2: true,\r\n      dialogInfos: {\r\n        dialogType: '',\r\n        form: {\r\n          uuid: null,\r\n          xml: '',\r\n          name: '',\r\n          hash: '',\r\n          start: '',\r\n          end: '',\r\n          step: ''\r\n        }\r\n      },\r\n      showDialog: false\r\n    };\r\n  },\r\n  mounted() {\r\n    var defaultPage = {\r\n      label: '监控客群属性集管理',\r\n      name: 'default',\r\n      slotName: 'firstTabPage',\r\n      closable: false\r\n    };\r\n    this.$refs.tabPage.$emit('open-tab', defaultPage);\r\n  },\r\n  methods: {\r\n    /**\r\n     * 清空form表单数据\r\n     */\r\n    clearForm() {\r\n      this.dialogInfos = {\r\n        dialogType: 'add',\r\n        form: {\r\n          uuid: '',\r\n          xml: '',\r\n          name: '',\r\n          hash: '',\r\n          start: '',\r\n          end: '',\r\n          step: ''\r\n        }\r\n      };\r\n    },\r\n    showFormDialog() {\r\n      this.showDialog = true;\r\n    },\r\n    closeFormDialog() {\r\n      this.showDialog = false;\r\n    },\r\n    /**\r\n     * 新增\r\n     */\r\n    add: function() {\r\n      this.dialogInfos.dialogType = 'add';\r\n      this.clearForm();\r\n      this.showFormDialog();\r\n    },\r\n    /**\r\n     * 编辑\r\n     */\r\n    edit: function(row) {\r\n      this.dialogInfos.dialogType = 'edit';\r\n      this.dialogInfos.form = {\r\n        uuid: row.uuid,\r\n        xml: row.xml,\r\n        name: row.name,\r\n        hash: row.hash,\r\n        start: row.start,\r\n        end: row.end,\r\n        step: row.step\r\n      };\r\n      this.showFormDialog();\r\n    },\r\n    remove: function(row) {\r\n      this.$confirm('确认删除此客群监控属性?', '提示', {\r\n        confirmButtonText: '确定',\r\n        cancelButtonText: '取消',\r\n        type: 'warning'\r\n      }).then(() => {\r\n        Common.ajax({\r\n          url: '/web/v1/attribute/' + row.uuid,\r\n          type: 'delete',\r\n          contentType: 'application/json',\r\n          data: JSON.stringify({}),\r\n          dataType: 'json',\r\n          success: res => {\r\n            this.$message({\r\n              type: 'success',\r\n              message: '删除成功!'\r\n            });\r\n            this.$refs.dataTable.$emit('load-data');\r\n          },\r\n          error: err => {\r\n            let error = err.message || err;\r\n            err.message &&\r\n              this.$alert(error, '', {\r\n                type: 'error',\r\n                confirmButtonText: '知道了',\r\n                callback: action => {}\r\n              });\r\n          }\r\n        });\r\n      });\r\n    },\r\n    /**\r\n     * 详情页面form表单数据提交\r\n     */\r\n    submitFormHandle(...args) {\r\n      let successMessage;\r\n      let opertype = args[0];\r\n      let type = 'POST';\r\n      let url = '/web/v1/attribute';\r\n      let { uuid, name, xml, hash, start, end, step } = args[1];\r\n      if (opertype == 'add') {\r\n        successMessage = '新增成功';\r\n      } else {\r\n        successMessage = '编辑成功';\r\n        type = 'put';\r\n        url = url + '/' + uuid;\r\n      }\r\n      Common.ajax({\r\n        url: url,\r\n        type: type,\r\n        data: JSON.stringify({\r\n          name,\r\n          xml,\r\n          hash,\r\n          start,\r\n          end,\r\n          step\r\n        }),\r\n        uploadFiles: true,\r\n        dataType: 'json',\r\n        contentType: 'application/json',\r\n        processData: false,\r\n        success: res => {\r\n          this.alertMessage = successMessage;\r\n          this.$alert(successMessage, '', {\r\n            type: 'success',\r\n            confirmButtonText: '知道了',\r\n            callback: action => {\r\n              this.closeFormDialog();\r\n            }\r\n          });\r\n        },\r\n        error: err => {\r\n          let error = err.message || err;\r\n          this.alertMessage = error;\r\n          err.message &&\r\n            this.$alert(this.alertMessage, '', {\r\n              type: 'error',\r\n              confirmButtonText: '知道了',\r\n              callback: action => {}\r\n            });\r\n        },\r\n        complete: () => {\r\n          this.$refs.dataTable.$emit('load-data');\r\n        }\r\n      });\r\n    },\r\n    // 格式化\r\n    formatterType(row, column) {\r\n      return row.hash === 1 ? '散列' : '连续';\r\n    }\r\n  }\r\n};\r\n</script>\r\n\r\n"]}]);
