<template>
  <div>
    <tab-page ref="tabPage">
      <template slot="firstTabPage">
        <data-table
          :data-table="dataTable"
          ref="dataTable"
          style="width: 100%"
          border
          :default-sort="{prop: 'date', order: 'descending'}"
        >
          <!--<data-table :data-table="dataTable" ref="dataTable">-->
          <div slot="conditions">
            <div class="opera-btn">
              <el-button type="primary" size="small" @click="add">&nbsp;新增属性</el-button>
            </div>
          </div>
          <template slot="columns">
            <el-table-column prop="uuid" label="uuid" align="center" width="70" sortable></el-table-column>
            <el-table-column prop="xml" label="变量XML字段" align="center" sortable></el-table-column>
            <el-table-column prop="name" label="释义" align="center" sortable></el-table-column>
            <el-table-column
              prop="hash"
              label="类型"
              align="center"
              width="80"
              :formatter="formatterType"
              sortable
            ></el-table-column>
            <el-table-column prop="step" label="步长" align="center" width="80" sortable></el-table-column>
            <el-table-column prop="start" label="开始范围" align="center" width="100" sortable></el-table-column>
            <el-table-column prop="end" label="结束范围" align="center" width="100" sortable></el-table-column>

            <el-table-column prop="created_at" label="创建日期" align="center" width="100" sortable></el-table-column>
            <el-table-column prop="updated_at" label="更新日期" align="center" width="100" sortable></el-table-column>
            <el-table-column label="操作" width="120" align="center">
              <template slot-scope="scope">
                <el-button type="primary" size="small" @click="edit(scope.row)">编辑</el-button>
                <el-button type="danger" size="small" @click="remove(scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </template>
        </data-table>

        <attribute-form
          :dialogInfo="dialogInfos"
          :dialogFormVisible="showDialog"
          @submitForm="submitFormHandle"
          v-on:closeDialog="closeFormDialog"
        ></attribute-form>
      </template>
    </tab-page>
  </div>
</template>

<style scoped>
.el-select .el-input {
  width: 100px;
}

.pagination_position {
  padding-top: 20px;
}

.echarts {
  height: 300px;
}

.opera-btn {
  margin-bottom: 10px;
}
</style>
<style>
.el-table .cell,
.el-table th > div {
  padding-left: 5px;
  padding-right: 5px;
  box-sizing: border-box;
  text-overflow: ellipsis;
}
</style>
<script>
import DataTable from '../../../components/DataTable';
import TabPage from '../../../components/TabPage';

import Common from '../../../utils/common';
import formatDate from '../../../utils/formatDate.js';

import attributeForm from './attributeForm.vue';

export default {
  components: {
    DataTable,
    TabPage,
    attributeForm
  },
  data() {
    let date = new Date();
    let prevMonth = new Date(date.setDate(date.getDate() - 30));
    let now = new Date();

    function randomData() {
      return Math.round(Math.random() * 1000);
    }

    return {
      detailData: {},
      deviceOptions: [
        {
          value: 'ALL',
          name: 'ALL'
        },
        {
          value: 'iPhone',
          name: 'iOS'
        },
        {
          value: 'Android',
          name: 'Android'
        }
      ],
      searchParam: {
        device: 'ALL',
        dateRange: [prevMonth, now]
      },
      dataTable: {
        pageSize: 10,
        pageSizes: [10, 20, 50, 100, 1000],
        type: 'get',
        url: '/web/v1/attribute',
        queryParams: {},
        emptyText: '没有记录'
      },
      loading2: true,
      dialogInfos: {
        dialogType: '',
        form: {
          uuid: null,
          xml: '',
          name: '',
          hash: '',
          start: '',
          end: '',
          step: ''
        }
      },
      showDialog: false
    };
  },
  mounted() {
    var defaultPage = {
      label: '监控客群属性集管理',
      name: 'default',
      slotName: 'firstTabPage',
      closable: false
    };
    this.$refs.tabPage.$emit('open-tab', defaultPage);
  },
  methods: {
    /**
     * 清空form表单数据
     */
    clearForm() {
      this.dialogInfos = {
        dialogType: 'add',
        form: {
          uuid: '',
          xml: '',
          name: '',
          hash: '',
          start: '',
          end: '',
          step: ''
        }
      };
    },
    showFormDialog() {
      this.showDialog = true;
    },
    closeFormDialog() {
      this.showDialog = false;
    },
    /**
     * 新增
     */
    add: function() {
      this.dialogInfos.dialogType = 'add';
      this.clearForm();
      this.showFormDialog();
    },
    /**
     * 编辑
     */
    edit: function(row) {
      this.dialogInfos.dialogType = 'edit';
      this.dialogInfos.form = {
        uuid: row.uuid,
        xml: row.xml,
        name: row.name,
        hash: row.hash,
        start: row.start,
        end: row.end,
        step: row.step
      };
      this.showFormDialog();
    },
    remove: function(row) {
      this.$confirm('确认删除此客群监控属性?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        Common.ajax({
          url: '/web/v1/attribute/' + row.uuid,
          type: 'delete',
          contentType: 'application/json',
          data: JSON.stringify({}),
          dataType: 'json',
          success: res => {
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            this.$refs.dataTable.$emit('load-data');
          },
          error: err => {
            let error = err.message || err;
            err.message &&
              this.$alert(error, '', {
                type: 'error',
                confirmButtonText: '知道了',
                callback: action => {}
              });
          }
        });
      });
    },
    /**
     * 详情页面form表单数据提交
     */
    submitFormHandle(...args) {
      let successMessage;
      let opertype = args[0];
      let type = 'POST';
      let url = '/web/v1/attribute';
      let { uuid, name, xml, hash, start, end, step } = args[1];
      if (opertype == 'add') {
        successMessage = '新增成功';
      } else {
        successMessage = '编辑成功';
        type = 'put';
        url = url + '/' + uuid;
      }
      Common.ajax({
        url: url,
        type: type,
        data: JSON.stringify({
          name,
          xml,
          hash,
          start,
          end,
          step
        }),
        uploadFiles: true,
        dataType: 'json',
        contentType: 'application/json',
        processData: false,
        success: res => {
          this.alertMessage = successMessage;
          this.$alert(successMessage, '', {
            type: 'success',
            confirmButtonText: '知道了',
            callback: action => {
              this.closeFormDialog();
            }
          });
        },
        error: err => {
          let error = err.message || err;
          this.alertMessage = error;
          err.message &&
            this.$alert(this.alertMessage, '', {
              type: 'error',
              confirmButtonText: '知道了',
              callback: action => {}
            });
        },
        complete: () => {
          this.$refs.dataTable.$emit('load-data');
        }
      });
    },
    // 格式化
    formatterType(row, column) {
      return row.hash === 1 ? '散列' : '连续';
    }
  }
};
</script>

