<template>
  <el-dialog
    ref="dialog"
    :title="dialogInfo.dialogType =='add'?'新增属性':'编辑属性'"
    width="50%"
    class="dialog-container"
    v-model="dialogVisible"
    :show-close="true"
    @close="closeDialog"
    :close-on-click-modal="false"
  >
    <div class="dialog-form">
      <el-form class="formInputWidth" :rules="rules" ref="ruleForm" :model="ruleForm">
        <el-form-item label="xml" :label-width="formLabelWidth" prop="xml">
          <el-input v-model="ruleForm.xml" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="释义" :label-width="formLabelWidth" prop="name">
          <el-input v-model="ruleForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="类型" :label-width="formLabelWidth" prop="hash">
          <el-select v-model="hash" placeholder="请选择类型">
            <el-option
              v-for="item in hashOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <div v-show="hash ===0">
          <el-form-item label="开始" :label-width="formLabelWidth" prop="start">
            <el-input v-model="ruleForm.start" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="结束" :label-width="formLabelWidth" prop="end">
            <el-input v-model="ruleForm.end" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="步长" :label-width="formLabelWidth" prop="step">
            <el-input v-model="ruleForm.step" auto-complete="off"></el-input>
          </el-form-item>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('ruleForm')">重置</el-button>
        <span class="wd">&nbsp;</span>
        <el-button type="primary" @click="submit('ruleForm')">确认</el-button>
      </div>
    </div>
  </el-dialog>
</template>

<style scoped>
.el-dialog__wrapper .el-dialog--customer .el-dialog__header {
  text-align: center;
}

.dialog-form {
  width: 380px;
}

.el-select {
  width: 200px;
}

.el-input {
  width: 200px;
}

.el-select .el-input {
  width: 260px;
}

.el-select-dropdown__item {
  max-width: 500px;
}

.dialog-footer {
  text-align: center;
}

.el-upload ul {
  display: none;
}

.download-btn {
  text-align: center;
  display: inline-block;
  font-size: 12px;
  border-radius: 5px;
  line-height: 30px;
  padding: 0 8px;
  outline: none;
  color: #ffffff;
  background-color: #20a0ff;
  box-sizing: border-box;
}

.wd {
  display: inline-block;
  width: 50px;
}

.self-line-height {
  line-height: 36px;
}

.self-line-height2 {
  line-height: 48px;
}

.label-wrapper {
  width: 100px;
}

.self-label {
  text-align: right;
  padding-right: 20px;
}

.el-row {
  height: 50px;
  line-height: 50px;
}

.el-form-item__label {
  width: 200px;
}

.text {
  font-size: 14px;
}

.item {
  padding: 10px 20px;
  margin: 10px 0;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}

.box-card {
  width: 300px;
  margin-right: 20px;
}

.txt-center {
  text-align: center;
}

.jc-between {
  justify-content: space-between;
}

.mt-30 {
  margin-top: 50px;
}

.left {
  float: right;
}

.form-content-width {
  width: 420px;
}

.mb-30 {
  margin-bottom: 22px;
}
</style>

<script>
import common from "../../../utils/common";

export default {
  components: {},
  props: ["dialogInfo", "dialogFormVisible"],
  data() {
    return {
      formLabelWidth: "100px",
      hash: 1,
      hashOptions: [
        {
          label: "散列",
          value: 1,
        },
        {
          label: "连续",
          value: 0,
        },
      ],
      ruleForm: {
        uuid: "",
        xml: "",
        name: "",
        hash: 1,
        step: "",
        start: "",
        end: "",
      },
      rules: {
        xml: [
          { required: true, message: "请输入xml属性字段", trigger: "blur" },
        ],
        name: [
          { required: true, message: "请输入释义", trigger: "blur" },
          {
            max: 24,
            message: "文案限制24个字符，包括标点符号",
            trigger: "change",
          },
        ],
        //          hash: [
        //            { required: true, message: '请选择属性类型', trigger: 'change' }
        //          ],
        step: [{ require: true, message: "请输入范围步长", trigger: "blur" }],
        start: [{ require: true, message: "请输入开始范围", trigger: "blur" }],
        end: [{ require: true, message: "请输入结束范围", trigger: "blur" }],
      },
      dialogVisible: false,
    };
  },
  computed: {},
  methods: {
    submit(formName) {
      if (!this.ruleForm.xml) {
        this.$message("请输入xml属性字段");
        return false;
      }
      if (!this.ruleForm.name) {
        this.$message("请输入释义");
        return false;
      } else {
        if (this.ruleForm.name.length > 24) {
          this.$message("文案限制24个字符，包括标点符号");
          return false;
        }
      }

      if (this.hash === 0) {
        if (!this.ruleForm.start) {
          this.$message("开始范围不能为空！");
          return false;
        }
        if (!this.ruleForm.end) {
          this.$message("结束范围不能为空！");
          return false;
        }
        if (!this.ruleForm.step) {
          this.$message("范围步长不能为空！");
          return false;
        }
      }

      this.ruleForm.hash = this.hash;

      this.$emit("submitForm", this.dialogInfo.dialogType, this.ruleForm);
    },

    resetForm(formName) {
      if (this.$refs[formName]) {
        this.$refs[formName].resetFields();
        this.ruleForm = {
          uuid: "",
          xml: "",
          name: "",
          hash: 1,
          step: "",
          start: "",
          end: "",
        };
        this.hash = 0;
      }
    },
    closeDialog() {
      //console.log('closeDialog');
      this.$emit("closeDialog");
    },
  },
  created() {},
  mounted() {
    this.ruleForm = this.dialogInfo.form;
  },
  watch: {
    dialogInfo: {
      handler: function(val, oldVal) {
        if (val.dialogType == "add") {
          this.resetForm("ruleForm");
        } else {
          this.ruleForm = this.dialogInfo.form;
          this.hash = this.ruleForm.hash;
          //console.log(this.ruleForm);
        }
      },
      deep: true,
    },
    //      id:function (val,oldVal) {
    //        this.ruleForm = this.dialogInfo.form;
    //      }
    dialogFormVisible: function(val) {
      this.dialogVisible = !!val;
    },
  },
};
</script>
